:host {
  --tag-color: #92A0B3;
  line-height: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #92A0B3;
}

.tag {
  display: inline;
  color: var(--tag-color);
  font-size: 10px;
  text-transform: uppercase;
}
.tag + .tag::before {
  content: "|";
  color: var(--tag-color);
  display: inline-block;
  height: 11px;
  margin: 0 6px -1px 6px;
}